function BasicTable() {
  const classes = useStyles();
  //const tmpRows = getRowsFromItems();
  const [rows, setRows] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredRows, setFilteredRows] = useState([]);
  const [sortUp, setSortUp] = useState(false);
  const [edit, setEdit] = useState(false);
  const [selectAll, setSelectAll] = useState(false);
  const handleAddItem = (rowIndex) => (event, values) => {
    const tmpRows = [...rows];

    tmpRows[rowIndex] = values.sort((a, b) => b.ID.localeCompare(a.localeCompare));
    tmpRows[rowIndex].parents = rows[rowIndex].parents;
    //tmpRows.sort((a, b) => a[0].ID.localeCompare(b[0].localeCompare));
    setRows(tmpRows);
  }
  const handleAddParent = (rowIndex) => (event, values) => {
    const tmpRows = [...rows];
    tmpRows[rowIndex].parents = [...values];
    setRows(tmpRows);
  }

  function handleSortIDs() {
    const tmpRows = [...rows];
    if (sortUp) {
      tmpRows.sort((a, b) => {
        var IDa = '';
        var IDb = '';
        if(a[0]) {
          IDa = a[0].ID;
        }
        if(b[0]){
          IDb = b[0].ID;
        }
        return IDa.localeCompare(IDb.localeCompare);
      });
      setSortUp(false);
    } else {
      
      tmpRows.sort((a, b) => {
        var IDa = '';
        var IDb = '';
        if(a[0]) {
          IDa = a[0].ID;
        }
        if(b[0]){
          IDb = b[0].ID;
        }
        return IDb.localeCompare(IDa.localeCompare);
      });
      setSortUp(true);
    }

    setRows(tmpRows);

  }

  function addRow() {
    const tmpRows = [...rows];
    const row = [];
    row.parents = [];
    row.checked = false;
    tmpRows.unshift(row);
    setRows(tmpRows);
  }

  function mergeIDs(row, i) {
    var ID = String(i);
    row.forEach(v => {
      ID += v.ID
    })
    return ID;
  }

  React.useEffect(() => {
    const filtredRows = new Array();
    if (searchTerm) {
      rows.forEach(row => {
        let added = false;
        row.forEach(value => {
          if (value.ID.toLowerCase().includes(searchTerm.toLowerCase())) {
            if (!added) {
              filtredRows.push(row);
              added = true;
            }
          }
        })
      })
      setFilteredRows(filtredRows);
    } else {
      setFilteredRows([...rows])
    }
  }, [searchTerm, rows]);

  React.useEffect(() => {
    const tmpRows = [...rows];
    tmpRows.forEach(row => {
      row.checked = selectAll;
    })
    setRows(tmpRows);
  }, [selectAll])

  const rowChecked = (index) => () => {
    const tmpRows = [...rows];
    tmpRows[index].checked = !tmpRows[index].checked;
    setRows(tmpRows);

  }

  function deleteSelected(){
    const tmpRows = [...rows];
    for(var i = tmpRows.length-1; i>=0; i--){
      if(tmpRows[i].checked){
        tmpRows.splice(i, 1);
      }
    }
    setRows(tmpRows);
  }

  function getIDOptionsForRow(row){
    if(row.length){
      return items.filter(item => item.category === row[0].category);
    } else if(row.parents.length){
      return items.filter(item => {
        let filter = true;
        row.parents.forEach(par => {
          if(item.category === par.category) {
            filter = false;
          }
        });
        return filter;
      })
    } else {
      return items
    }
  }

  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            {
              edit ? <TableCell width="30px"> <Checkbox color="primary" checked={selectAll} onChange={()=>setSelectAll(!selectAll)} /></TableCell> : <></>
            }
            <TableCell>
              <Button endIcon={sortUp ? < ArrowUpward /> : <ArrowDownwardIcon />} onClick={handleSortIDs} > ID </Button>
              <TextField onChange={(e) => { setSearchTerm(e.target.value) }} placeholder="Vyhledat ID" InputProps={{
                endAdornment: (
                  <InputAdornment position="start">
                    <SearchOutlined />
                  </InputAdornment>
                ),
              }}></TextField>
            </TableCell>
            <TableCell >Umístění
              {
                edit ? <Button style={{ float: 'right', backgroundColor: 'red', color: 'white' }} onClick={deleteSelected} > Delete selected rows </Button> : <></>
              }
              <Button style={{ float: 'right', backgroundColor: 'green', color: 'white', marginRight: '20px' }} onClick={addRow} > Add Row</Button>
              <Button style={{ float: 'right', backgroundColor: 'orange', color: 'white', marginRight: '20px' }} onClick={() => {setEdit(!edit)}} > Edit</Button>
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {filteredRows.map((row, i) => (
            <TableRow key={mergeIDs(row, i)}>
              {
                edit ? <TableCell width="30px"> <Checkbox checked={row.checked} onChange={rowChecked(i)} color="primary" />
                </TableCell> : <></>
              }
              <TableCell>
                <Autocomplete
                  multiple
                  options={ getIDOptionsForRow(row)}
                  groupBy={(item) => item.category}
                  getOptionLabel={(option) => option.ID}
                  value={row}
                  onChange={handleAddItem(i)}
                  style={{ width: '300px', color: '#000000' }}
                  renderInput={(params) => <TextField {...params} label="ID položky" color='primary' />}
                />
              </TableCell>
              <TableCell>
                <Autocomplete
                  multiple
                  options={row.length ? items.filter(item => item.category !== row[0].category) : items}
                  groupBy={(item) => item.category}
                  getOptionLabel={(option) => option.ID}
                  style={{ width: '650px', color: '#000000' }}
                  renderInput={(params) => <TextField {...params} label="ID položky" color='primary' />}
                  onChange={handleAddParent(i)}
                  value={row.parents ? row.parents : []}
                />
              </TableCell>

            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}


const theme = createTheme({
  typography: {
    h2: {
      fontSize: 36,
      marginBottom: 0
    }
  },
  palette: {
    primary: {
      main: orange[400],
    },
    secondary: {
      main: green[400]
    },
    text: {
      primary: "#000000"
    }
  }
})



function App() {

  return (
    <ThemeProvider theme={theme}>
      <div >
        <header>
          <BasicTable />
        </header>
      </div>
    </ThemeProvider>
  );
}

export default App;
